<template>
  <div>
    <div class="vld-parent">
      <loading :active.sync="isLoading"></loading>
    </div>
    <div class="text-right">
      <button class="btn btn-outline-primary btn-sm" @click="openModal(true)"></button>
      <table class="table mt-4">
        <thead>
          <th width="150"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="150"></th>
        </thead>
        <tbody class="card-tbody">
          <tr v-for="item in products" :key="item.id">
            <td>{{item.category}}</td>
            <td>{{item.title}}</td>
            <td class="text-right">{{item.origin_price | currency}}</td>
            <td class="text-right">{{item.price | currency}}</td>
            <td>
              <span v-if="item.is_enabled"></span>
              <span v-else></span>
            </td>
            <td class="flex-seprate">
              <button class="btn btn-outline-primary btn-sm mr-1" @click="openModal(false, item)"></button>
              <button class="btn btn-outline-primary btn-sm" @click="removeProduct(item)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <Pagination :pages="pagination" @callPage="getProducts"></Pagination>
      <div
        class="modal fade"
        id="productModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
      <!-- Modal Start -->
        <div class="modal-dialog modal-lg" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-sm-4" style="text-align: -webkit-left;">
                  <div class="form-group">
                    <label for="image"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="image"
                      placeholder=""
                      v-model="tempProducts.imageUrl"
                    >
                  </div>
                  <div class="form-group">
                    <label for="customFile">
                       
                      <i class="fas fa-spinner fa-spin" v-if="uploadFileLoading"></i>
                    </label>
                    <input
                      type="file"
                      id="customFile"
                      class="form-control"
                      ref="files"
                      @change="uploadFile"
                    >
                  </div>
                  <img :src="tempProducts.imageUrl" class="img-fluid" alt>
                </div>
                <div class="col-sm-8" style="text-align: -webkit-left;">
                  <div class="form-group">
                    <label for="title"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="title"
                      placeholder=""
                      v-model="tempProducts.title"
                    >
                  </div>
                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="category"></label>
                      <input
                        type="text"
                        class="form-control"
                        id="category"
                        placeholder=""
                        v-model.trim="tempProducts.category"
                      >
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price"></label>
                      <input
                        type="unit"
                        class="form-control"
                        id="unit"
                        placeholder=""
                        v-model="tempProducts.unit"
                      >
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="origin_price"></label>
                      <input
                        type="number"
                        class="form-control"
                        id="origin_price"
                        placeholder=""
                        v-model="tempProducts.origin_price"
                      >
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price"></label>
                      <input
                        type="number"
                        class="form-control"
                        id="price"
                        placeholder=""
                        v-model="tempProducts.price"
                      >
                    </div>
                  </div>
                  <hr>
                  <div class="form-group">
                    <label for="description"></label>
                    <textarea
                      type="text"
                      class="form-control"
                      id="description"
                      placeholder=""
                      v-model="tempProducts.description"
                    ></textarea>
                  </div>
                  <div class="form-group">
                    <label for="content"></label>
                    <textarea
                      type="text"
                      class="form-control"
                      id="content"
                      placeholder=""
                      v-model="tempProducts.content"
                    ></textarea>
                  </div>
                  <div class="form-group">
                    <div class="form-check">
                      <input
                        class="form-check-input"
                        type="checkbox"
                        id="is_enabled"
                        v-model="tempProducts.is_enabled"
                        :true-value="1"
                        :false-value="0"
                      >
                      <label class="form-check-label" for="is_enabled"></label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
              <button type="button" class="btn btn-primary" @click="updateProduct"></button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination.vue";
export default {
  data: function() {
    return {
      products: [],
      tempProducts: {},
      isNew: false,
      pagination: {},
      uploadFileLoading: false,
      isLoading: false
    };
  },
  components: {
    Pagination
  },
  methods: {
    getProducts: function(page = 1) {
      let vm = this;
      vm.isLoading = true;
      // /api/:api_path/admin/products?page=:page
      const api = `https://vue-course-api.hexschool.io/api/jamespantest1/admin/products?page=${page}`;
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
      });
    },
    //  openModal  isNew === true tempProducts  ( Modal )
    //  isNew = true
    openModal: function(isNew, item) {
      var vm = this;
      $("#productModal").modal("show");
      if (isNew) {
        vm.tempProducts = {};
        vm.isNew = true;
      } else {
        vm.tempProducts = Object.assign({}, item);
        vm.isNew = false;
      }
      // console.log(isNew);
    },
    updateProduct: function() {
      let vm = this;
      var httpChange = "post";
      var api =
        "https://vue-course-api.hexschool.io/api/jamespantest1/admin/product";
      // false
      if (!vm.isNew) {
        httpChange = "put";
        api = `https://vue-course-api.hexschool.io/api/jamespantest1/admin/product/${
          vm.tempProducts.id
        }`;
      }
      this.$http[httpChange](api, { data: vm.tempProducts }).then(response => {
        console.log(response.data);
        if (response.data.success) {
          $("#productModal").modal("hide");
          vm.getProducts();
        }
      });
      // console.log(vm.tempProducts);
    },
    removeProduct: function(item) {
      let vm = this;
      const api = `https://vue-course-api.hexschool.io/api/jamespantest1/admin/product/${
        item.id
      }`;
      this.$http.delete(api).then(response => {
        // console.log(response.data)
        if (response.data.success) {
          vm.getProducts();
        }
      });
    },
    uploadFile: function() {
      let vm = this;
      vm.uploadFileLoading = true;
      const api = `https://vue-course-api.hexschool.io/api/jamespantest1/admin/upload`;
      console.log(this);
      const uploadedFile = this.$refs.files.files[0];
      var formData = new FormData();
      formData.append("file-to-upload", uploadedFile);
      this.$http
        .post(api, formData, {
          header: {
            "Content-type": "multipart/form-data"
          }
        })
        .then(response => {
          if (response.data.success) {
            // vm.tempProducts.imageUrl = response.data.imageUrl;
            console.log(response.data);
            //  imageUrl  tempProducts response.data.imageUrl
            vm.$set(vm.tempProducts, "imageUrl", response.data.imageUrl);
            vm.uploadFileLoading = false;
          } else {
            this.$bus.$emit("alertMessage", response.data.message, "danger");
          }
        });
    }
  },
  created() {
    this.getProducts();
  }
};
</script>

