<template>
  <div>
    <div class="vld-parent">
      <loading :active.sync="isLoading"></loading>
    </div>
    <div class="text-right">
      <button class="btn btn-outline-primary btn-sm" @click="openModal(true)">
      </button>
      <table class="table mt-4">
        <thead>
          <th width="150"></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="150"></th>
        </thead>
        <tbody v-for="item in coupons" :key="item.id" class="card-tbody">
          <tr>
            <td>{{ item.title }}</td>
            <td>{{ item.percent }}</td>
            <td class="text-right">{{ item.due_date }}</td>
            <td>
              <span v-if="item.is_enabled === 1"></span>
              <span v-else></span>
            </td>
            <td class="flex-seprate">
              <button class="btn btn-outline-primary btn-sm mr-1" @click="openModal(false, item)"></button>
              <button class="btn btn-outline-primary btn-sm" @click="removeCoupons(item)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <div
        class="modal fade"
        id="productModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-lg" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="w-100" style="text-align: -webkit-left;">
                  <div class="form-group">
                    <label for="title"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="title"
                      placeholder=""
                      v-model="tempCoupons.title"
                    >
                  </div>
                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="category"></label>
                      <input
                        type="text"
                        class="form-control"
                        id="category"
                        placeholder=""
                        v-model="tempCoupons.percent"
                      >
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price"></label>
                      <input
                        type="unit"
                        class="form-control"
                        id="unit"
                        placeholder=""
                        v-model="tempCoupons.due_date"
                      >
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="form-group col-md-6 w-100">
                      <label for="origin_price"></label>
                      <input
                        type="number"
                        class="form-control"
                        id="origin_price"
                        placeholder=""
                        v-model="tempCoupons.code"
                      >
                    </div>
                  </div>
                  <hr>
                  <div class="form-group">
                    <div class="form-check">
                      <input
                        class="form-check-input"
                        type="checkbox"
                        id="is_enabled"
                        v-model="tempCoupons.is_enabled"
                        :true-value="1"
                        :false-value="0"
                      >
                      <label class="form-check-label" for="is_enabled"></label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
              <button type="button" class="btn btn-primary" @click="updateCoupons"></button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination.vue";
export default {
  data: function() {
    return {
      coupons: [],
      tempCoupons: {
        title: "",
        is_enabled: 0,
        percent: 100,
        due_date: 0,
        code: ""
      },
      due_date: new Date(),
      isNew: false,
      pagination: {},
      uploadFileLoading: false,
      isLoading: false
    };
  },
  methods: {
    getCoupons: function() {
      var vm = this;
      vm.isLoading = true;
      const api =
        "https://vue-course-api.hexschool.io/api/jamespantest1/admin/coupons";
      this.$http.get(api).then(response => {
        // console.log(response.data);
        if (response.data.success) {
          vm.isLoading = false;
          vm.coupons = response.data.coupons;
          console.log(vm.coupons);
        }
      });
    },
    updateCoupons: function() {
      // /api/:api_path/admin/coupon/:id
      var vm = this;
      vm.isLoading = true;
      //  isNew  true  api
      var httpChange = "post";
      var api =
        "https://vue-course-api.hexschool.io/api/jamespantest1/admin/coupon";

      //  isNew  false api ()
      if (!vm.isNew) {
        httpChange = "put";
        var api = `https://vue-course-api.hexschool.io/api/jamespantest1/admin/coupon/${
          vm.tempCoupons.id
        }`;
      }

      this.$http[httpChange](api, { data: vm.tempCoupons }).then(response => {
        vm.isLoading = false;
        // console.log(response.data);
        vm.coupons = response.data.coupone;
        $("#productModal").modal("hide");
        vm.getCoupons();
      });
    },
    removeCoupons: function(item) {
      // api/:api_path/admin/coupon/:coupon_id
      var vm = this;
      const api = `https://vue-course-api.hexschool.io/api/jamespantest1/admin/coupon/${
        item.id
      }`;
      this.$http.delete(api).then(response => {
        // console.log(response.data);
        if (response.data.success) {
          vm.getCoupons();
        }
      });
    },
    openModal: function(isNew, item) {
      var vm = this;
      $("#productModal").modal("show");
      if (isNew) {
        vm.tempCoupons = {};
        vm.isNew = true;
      } else {
        vm.tempCoupons = Object.assign({}, item);
        vm.isNew = false;
      }
    }
  },
  created() {
    this.getCoupons();
  },
  watch: {
    due_date() {
      const vm = this;
      const timestamp = Math.floor(new Date(vm.due_date) / 1000);
      vm.tempCoupon.due_date = timestamp;
    }
  }
};
</script>